import kaplay from "kaplay";
import { crew } from "@kaplayjs/crew";


// Declaring the K constant

const k = kaplay({
  plugins: [crew],
  width: 1920,
  height: 1080,
  letterbox: true,
  background: [0, 0, 0],
  global: false,
  touchToMouse: true,
  buttons: {
    jump: {
      keyboard: ["space"],
      mouse: "left",
    }
  },
  debugKey: ";",
});

// Exporting the K constant

export default k;