import k from "../kaplayCTX";


export function makeSonic(pos) {
  const sonic = k.add([
    k.sprite("sonic", { anim: "run" }),
    k.scale(4),
    k.area(),
    k.body({ jumpForce: 1700 }),
    k.anchor("center"),
    k.pos(pos),
    {
      ringCollectUI: null,
      setControls() {
        sonic.onButtonPress("jump", () => {
          if (sonic.isGrounded()) {
            sonic.play("jump");
            sonic.jump();
            k.play("jump", { volume: 0.5 });
          }
        });
      },
      setEvents() {
        sonic.onGround(() => {
          sonic.play("run");
        });
      },
    },
  ]);

  sonic.ringCollectUI = sonic.add([
    k.text("", { font: "mania", size: 24 }),
    k.color(255, 255, 0),
    k.anchor("center"),
    k.pos(30, -10),
  ])

  return sonic;
}

