import k from "../kaplayCTX";
import { makeSonic } from "../Entities/sonic";
import { makeMotobug } from "../Entities/motobug";
import { makeRing } from "../Entities/ring";

export default function game() {
    k.setGravity(3100);
    const citysfx = k.play("city", { loop: true, volume: 0.2 });

    const bgWidth = 1920;
    const bgPieces = [
        k.add([
            k.sprite("chemical-bg"),
            k.pos(0, 0),
            k.scale(2),
            k.opacity(0.8),
        ]),
        k.add([
            k.sprite("chemical-bg"),
            k.pos(bgWidth * 2, 0),
            k.scale(2),
            k.opacity(0.8),
        ]),
    ];

    const platformWidth = 1280;
    const platforms = [
        k.add([
            k.sprite("platform"),
            k.scale(4),
            k.pos(0, 450),
        ]),
        k.add([
            k.sprite("platform"),
            k.scale(4),
            k.pos(platformWidth * 4, 450),
        ]),
    ];

    let score = 0;
    let scoreMultiplier = 0;

    const scoreText = k.add([
        k.text("SCORE : 0", { font: "mania", size: 72 }),
        k.pos(20, 20),
    ]);


    const sonic = makeSonic(k.vec2(200, 745));
    sonic.setControls();
    sonic.setEvents();
    sonic.onCollide("enemy", (enemy) => {
        if (!sonic.isGrounded()) {
            k.play("destroy", { volume: 0.5 });
            k.play("hyper-ring", { volume: 0.5 });
            k.destroy(enemy);
            sonic.play("jump");
            sonic.jump();
            scoreMultiplier += 1;
            score += 10 * scoreMultiplier;
            scoreText.text = `SCORE : ${score}`;
            if (scoreMultiplier === 1) {
                sonic.ringCollectUI.text = `+10`;
            }
            if (scoreMultiplier > 1) {
                sonic.ringCollectUI.text = `x${scoreMultiplier}}`;
            }
            k.wait(1, () => {
                sonic.ringCollectUI.text = "";
            })
            return;
        }

        k.play("hurt", { volume: 0.5 });
        k.setData("current-score", score);
        k.go("gameover", { citysfx });
    });
    sonic.onCollide("ring", (ring) => {
            k.play("ring", { volume: 0.5 });
            k.destroy(ring);
            score++;
            scoreText.text = `SCORE : ${score}`;
            sonic.ringCollectUI.text = `+1`;
            k.wait(1, () => {
                sonic.ringCollectUI.text = "";
            })
    });

    let gameSpeed = 300;

    // Spawns ONE motobug, then schedules the NEXT spawn exactly once.
    const spawnMotobug = () => {
        const motobug = makeMotobug(k.vec2(1950, 773));

        motobug.onUpdate(() => {
            // Smooth speed with a cap (prevents the 299-unit "jump" at 3000)
            const speed = Math.min(gameSpeed + 300, 3000);
            motobug.move(-speed, 0);

            // Destroy when it’s past the left edge (avoid relying on isOffScreen unless you added that component)
            if (motobug.pos.x < -50) {
                motobug.destroy();
            }
        });

        // Schedule the next spawn ONCE (not every frame)
        const waitTime = k.rand(0.5, 2.5);
        k.wait(waitTime, spawnMotobug);
    };

    spawnMotobug();

    const spawnRing = () => {
        const ring = makeRing(k.vec2(1950, 745));
        ring.onUpdate(() => {
            ring.move(-gameSpeed, 0);
        });
        ring.onExitScreen(() => {
            if (ring.pos.x < 0) k.destroy(ring);
        });

        const waitTime = k.rand(0.5, 3);
        k.wait(waitTime, spawnRing);
    };

    spawnRing();

    let bgSpeed = gameSpeed / 20;
    let bgJump = gameSpeed / 6;
    k.loop(1, () => {
        gameSpeed += 50;
        bgSpeed = gameSpeed / 20;
        bgJump = gameSpeed / 6;
    });

    k.add([
        k.rect(1940, 300),
        k.opacity(0),
        k.area(),
        k.body({ isStatic: true }),
        k.pos(0, 832),
    ]);

    k.onUpdate(() => {
        if (sonic.isGrounded()) {
            scoreMultiplier = 0;
        }

        if (bgPieces[1].pos.x < 0) {
            bgPieces[0].moveTo(bgPieces[1].pos.x + bgWidth * 2);
            bgPieces.push(bgPieces.shift());
        }

        bgPieces[0].move(-bgSpeed, 0)
        bgPieces[1].moveTo(bgPieces[0].pos.x + bgWidth * 2, 0)
        bgPieces[0].moveTo(bgPieces[0].pos.x, -sonic.pos.y / 10 - 50);
        bgPieces[1].moveTo(bgPieces[1].pos.x, -sonic.pos.y / 10 - 50);

        if (platforms[1].pos.x < 0) {
            platforms[0].moveTo(platforms[1].pos.x + platforms[1].width * 4, 450);
            platforms.push(platforms.shift());
        }

        platforms[0].move(-gameSpeed, 0);
        platforms[1].moveTo(platforms[0].pos.x + platforms[1].width * 4, 450);
    });
}