// src/plugin.ts
var crew = (k) => {
  return {
    loadCrew(kind, crew2, name) {
      name = name ?? crew2;
      const image = crew2.endsWith("-o") ? "outlined" : "sprite";
      const crewKey = crew2.replace(/-o$/, "");
      const crewData = assets[crewKey];
      if (!crewData) {
        throw new Error(`Crew ${crew2} not found`);
      }
      if (crewData.kind === "Sprite") {
        return k.loadSprite(
          name,
          crewData?.spritesheet ? crewData.spritesheet[image] : crewData[image],
          crewData?.loadSpriteOpt
        );
      } else if (crewData.kind === "Font") {
        return k.loadBitmapFont(
          name,
          crewData[image],
          crewData.width,
          crewData.height
        );
      } else if (crewData.kind === "Sound") {
        return k.loadSound(
          name,
          crewData.sound
        );
      }
    }
  };
};

